import React, { memo, useCallback, useMemo } from "react";
import { Fonts, styled } from "@true/core";
import moment from "moment";
import { openDetailEventScreen } from "@/utils";
import { useEvents } from "@/store/events";
import { Buffer } from "buffer";
import { IBase64 } from "@/utils/helper";
import FastImage from "react-native-fast-image";
import { getChannels } from "@/store/channels";

interface EventThumbnailProps {
  id: number;
}

export const EventThumbnail = memo(function(props: EventThumbnailProps) {
  const { id } = props;
  const data = useEvents(id);
  const coverImage: IBase64 = JSON.parse(
    Buffer.from(data?.cover || "", "base64").toString("utf-8")
  );
  const openDetailScreen = useCallback(() => {
    openDetailEventScreen({
      id: id
    });
  }, []);
  const channelName = useMemo(()=>{
    return getChannels(data?.channel_id || '')?.name
  },[])
  return (
    <SContainer>
      <SContentContainer onPress={openDetailScreen}>
        <SImageWrapper>
          <SChannelBox>
            <STextChannel>{channelName}</STextChannel>
          </SChannelBox>
          <SCoverImage resizeMode={"cover"} source={{ uri: coverImage.image }} />
        </SImageWrapper>
        <SInfoWrapper>
          <STextDate>{moment(
            // @ts-ignore
            data?.time * 1000
          ).format("dddd, HH:mm DD/MM/YYYY")}</STextDate>
          <STextTitle>{data?.name}</STextTitle>
          <SRow>
            <STextInfo>{`${data?.attendees.length} attendees`}</STextInfo>
            <SDotView />
            <STextInfo>{data?.address}</STextInfo>
          </SRow>
        </SInfoWrapper>
      </SContentContainer>
    </SContainer>
  );
});
const SContainer = styled.View`
  background-color: transparent;
`;
const SContentContainer = styled.TouchableOpacity`
  background-color: white;
  border-radius: 8px;
  border: 1px solid #ebebeb;
  margin: 16px 16px 0 16px;
  padding: 12px;
`;
const SImageWrapper = styled.View`
  width: 100%;
`;
const SCoverImage = styled(FastImage)`
  width: 100%;
  height: 180px;
  max-height: 180px;
  border-radius: 8px;
  background-color: #2a91d6;
`;

const SInfoWrapper = styled.View`
  align-items: flex-start;
  justify-content: center;
`;

const SRow = styled.View`
  flex-direction: row;
  align-items: center;
`;
const STextDate = styled.Text`
  color: ${p => p.theme.neutral2};
  font-family: ${Fonts.Medium};
  font-size: 14px;
  line-height: 20px;
  margin-top: 8px
`;

const STextTitle = styled.Text.attrs(props => ({
  numberOfLines: 2
}))`
  color: ${p => p.theme.neutral1};
  font-family: ${Fonts.Regular};
  font-size: 16px;
  line-height: 24px; 
  margin-top: 4px
`;
const STextInfo = styled.Text`
  color: ${p => p.theme.neutral2};
  font-family: ${Fonts.Medium};
  font-size: 12px;
  line-height: 16px;
  margin-top: 8px
`;
const SDotView = styled.View`
  background-color: ${p => p.theme.neutral3};
  width: 4px;
  height: 4px;
  border-radius: 2px;
  margin: 8px 6px 0 6px;
`;
const SChannelBox = styled.View`
  display: inline-flex;
  padding: 4px 8px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  border-radius: 40px;
  background-color: rgba(0, 0, 0, 0.65);
  position: absolute;
  top:8px;
  left: 8px;
  z-index: 10;
`
const STextChannel = styled.Text`
  color: #fff;
  /* Caption/caption-medium-500 */
  font-family: ${Fonts.Medium};
  font-size: 12px;
  line-height: 16px; /* 133.333% */
`
