import React, {memo, useCallback, useState} from 'react';
import {CustomHeader} from '@/components/CustomHeader';
import {normalizeStringForSearch} from '@true/core';
import {navigateToCreateEvent} from '@/utils';
import {ScreenWrapper, ScrollableTabView} from '@true/ui';
import {EventThumbnail} from '@/screens/EventScreen/EventThumbnail';
import {getEvents, getEventsByQuery, useEventsByQuery} from '@/store/events';
import {
  requestJoinedEvents,
  requestPublicEvents,
} from '@/store/events/function';
import {IFilterParams} from '@/screens/SearchScreen/type';

export const EventScreen = memo(function () {
  const [params, setParams] = useState<IFilterParams>({});
  const [search, setSearch] = useState('');
  const onChangeText = useCallback(
    async (q: string) => {
      setSearch(q);
    },
    [params],
  );

  const onSetData = useCallback(
    (prevData: string[]) => {
      return (prevData || []).filter(item =>
        normalizeStringForSearch(
          getEvents(item as string)?.name || '',
        ).includes(search),
      );
    },
    [search],
  );
  const renderItem = useCallback(
    (item: string | number, index?: number, isLast?: boolean) => {
      return <EventThumbnail id={item as number} />;
    },
    [],
  );
  return (
    <ScreenWrapper isGrey={true}>
      <CustomHeader
        title={'Events'}
        isBack
        onCreate={() => {
          navigateToCreateEvent({id: -1});
        }}
        isSearch
        onChangeText={onChangeText}
      />
      <ScrollableTabView.Provider
        params={{}}
        renderItem={renderItem}
        getItemByQuery={getEventsByQuery}
        useItemByQuery={useEventsByQuery}
        tabs={[
          {
            title: 'Public events',
            content: (p: any) => (
              <ScrollableTabView.List
                {...p}
                itemHeight={190}
                index={0}
                requestFn={requestPublicEvents}
                setData={onSetData}
              />
            ),
          },
          {
            title: 'Joined events',
            content: (p: any) => (
              <ScrollableTabView.List
                {...p}
                itemHeight={190}
                index={1}
                requestFn={requestJoinedEvents}
                query={'joined'}
                setData={onSetData}
              />
            ),
          },
        ]}>
        <ScrollableTabView.CollapsableTabBar />
        <ScrollableTabView.Content />
      </ScrollableTabView.Provider>
    </ScreenWrapper>
  );
});
