import {Core, Fetch, ToastService} from '@true/core';
import {ICreateEvent, IEvent} from '@/store/events/type';
import {batch} from 'react-redux';
import {setEventsQuery, syncEvents} from '@/store/events/index';
import {getUpdates, syncUpdates} from '@/store/updates';

export const requestPublicEvents = async () => {
  const {data} = await Fetch.postWithToken<{
    events: IEvent[];
  }>(`@${Core.systemPath}/inside/ajax/api/event/gets`);

  batch(() => {
    if (data.events.length) {
      syncEvents(data.events);
      setEventsQuery(prev => ({
        ...prev,
        ['all']: data.events.map(item => item.id.toString()),
      }));
    }
  });
  return data.events;
};

