export const requestEvent = async (params: {event_id: string | number}) => {
  const {data} = await Fetch.postWithToken<{
    event: IEvent;
  }>(
    `@${Core.systemPath}/inside/ajax/api/event/get?event_id=${params.event_id}`,
  );

  batch(() => {
    if (data.event) {
      syncEvents([data.event]);
    }
  });
  return data.event;
};

export const requestCreateEvent = async (params: ICreateEvent) => {
  const {data} = await Fetch.postWithToken<{
    event: IEvent;
  }>(`@${Core.systemPath}/inside/ajax/api/event/create`, params);
  batch(() => {
    if (data.event) {
      syncEvents([data.event]);
      setEventsQuery(prev => ({
        ...prev,
        ['all']: [data.event.id.toString(), ...(prev['all'] || [])],
      }));
    }
  });
};

export const requestEditEvent = async (
  params: ICreateEvent,
  updateId?: number,
) => {
  const {data} = await Fetch.postWithToken<{
    event: IEvent;
  }>(`@${Core.systemPath}/inside/ajax/api/event/edit`, params);
  batch(() => {
    if (data.event) {
      if (updateId && updateId > 0) {
        const update = getUpdates(updateId);
        update &&
          syncUpdates([
            {
              ...update,
              attachment: data.event as any,
            },
          ]);
      }
      syncEvents([data.event]);
    }
  });
};

export const requestRemoveEvent = async (params: {id: number}) => {
  const {data} = await Fetch.postWithToken<{
    message: string;
  }>(`@${Core.systemPath}/inside/ajax/api/event/remove`, params);
  batch(() => {
    if (data.message === 'ok') {
      setEventsQuery(prev => ({
        ...prev,
        ['all']: (prev['all'] || []).filter(
          item => item !== params.id.toString(),
        ),
      }));
    }
  });
};

export const requestAttendEvent = async (params: {
  id: string | number;
  data: IEvent;
  myId: string | number;
}) => {
  const {data} = await Fetch.postWithToken<{
    message: string;
  }>(`@${Core.systemPath}/inside/ajax/api/event/attendee/attend`, params);
  if (data.message == 'ok') {
    syncEvents([
      {
        ...params.data,
        attendees: [
          ...new Set([...params.data.attendees, Number(params.myId)]),
        ],
      },
    ]);
    ToastService.show('Attend event successfully');
  }
};

export const requestInviteEvent = async (params: {
  id: string | number;
  data?: IEvent;
  channel_id: string | number;
  ids: number[];
  guests: string;
}) => {
  const {data} = await Fetch.postWithToken<{
    message: string;
  }>(`@${Core.systemPath}/inside/ajax/api/event/attendee/invite`, params);
  if (data.message == 'ok' && params.data) {
    syncEvents([
      {
        ...params.data,
        attendees: [...new Set([...params.data.attendees, ...params.ids])],
      },
    ]);
    ToastService.show('Invite event successfully');
  }
};
